/* 全局 */
* {
    /* outline: 1px black solid; */
    list-style: none;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0;
    padding: 0;
}
a {
    text-decoration: none;
    color: #333;
}
html {
    font-size: 11px;
}
body {
    margin: 0;
}
main {
    background-color: #F6EFEF;
}
.page {
    background-color: #F6EFEF;
}
/* 布局类 */
.wrapper {
    width: 95%;
    max-width: 80rem;
    margin: 0 auto;
}
.flex-box {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
/* 导航 */
nav {
    /* height: 4rem; */
    -webkit-box-shadow: 0.3rem 0.6rem 1rem rgba(0,0,0,0.3);
            box-shadow: 0.3rem 0.6rem 1rem rgba(0,0,0,0.3);
    position: fixed;
    width: 100%;
    z-index: 1;
    background-color:rgba(231, 228, 228,0.5);
}
nav a {
    display: inline-block;
   
    line-height: 4.5rem;
    text-align: center;
}
.nav-left a{
    color:#3D5870;
    font-size: 2rem;
    font-weight: 700;
    margin: auto 1rem;
}
.nav-left a::first-letter {
    color: coral;
    font-size: 2.5rem;
}
.nav-center a{
    
    height: 4.5rem;
    /* width: 100%; */
    width: 4.5rem;
    font-size: 2rem;
    font-weight: 500;
    color: #F6EFEF;
    background-color:#3D5870;
    border-radius: 999rem;
    margin: 0 5px;
}
.nav-right a {
    font-size: 1.5rem;
    /* background-color: #fff; */
    width: 5rem;
    height: 2.5rem;
    line-height: 2.5rem;
    margin-top: 1rem;
    border-radius: 999rem;
    margin-right: 1rem;
    color:#3D5870;
}
.nav-right li:last-child a {
    background-color: rgb(94, 192, 48);
    color:rgb(247, 245, 241);

}
/* 头部 */
main:before {
    content: "";
	display:block;
	clear: both;
	height: 5rem;
}
.header-bg {
    height: 20rem;
    background-image: url(./static/img/header.9aba67f.jpg);
    background-repeat: no-repeat;
    background-position: 160px -160px;
    background-size: 100% auto;
}
.header-img {
    width: 100%;
    height: 100%;
    max-width: 300rem;
}
.header-show {
    /* height: 30rem; */
    position: relative;
}
.header-show:after {
	content: "";
	display:block;
	clear: both;
	height: 0;
}
.slogan {
    float: left;
    height: 30rem;
    width: 30rem;
    margin-left: 0rem;
    margin-top: 0rem;
    background-color: #FABD4E;
}
.slogan h1 {
    font-weight: normal;
    font-size: 5.5rem;
    margin-top: 5rem;
    text-indent: 1rem;
}
.slogan p {
    font-weight: 100;
    margin-left: 1rem;
    /* text-indent: 1rem; */
    margin-top: 1rem;
    font-size: 4rem;
}
.join {
    float: right;
    height: 17rem;
    width: 37rem;
    /* margin-right: 8rem; */
    margin-top: 6rem;
    background-color: #3D5870;

}
.join a {
    display: inline-block;
    width:100%;
    height:100%;
    line-height: 15.5rem;
    text-align: center;
    color:#D4DADA ;
    font-size: 7rem;
}
.pull {
    display: inline-block;
    position: absolute;
    bottom: 0rem;
    left: 45%;
    width: 3rem;
}
.pull img {
    width: 100%;
}
/* 主要内容 */
section {
    margin: 4rem auto;

}
section>div {
    height: 18rem;
    background-attachment: fixed;
    
}
article {
    /* height: 22rem; */
    margin-top: 1rem;
    border: 3px #3D5870 dotted;
    padding: 0.5rem;
}
article h2 {
    font-size: 2.5rem;
    float: left;
    margin: 1rem;
}
article p {
    font-size: 1.5rem;

    /* text-indent: 3rem; */
}
.bg1 {
    background-image: url(./static/img/pain.165fa77.jpg);
    background-position: center -5rem;
    background-repeat: no-repeat;
}
.bg2 {
    background-image: url(./static/img/Laocoon_and_His_Sons.a67f918.jpg);
    background-repeat: no-repeat;
    background-position: center 4rem;
}
.bg3 {
    background-image: url(./static/img/architecture.2c5315d.jpg);
    background-position: 0 -9rem;
}
.bg4 {
    background-image: url(./static/img/music.1c9b1c9.jpg);
    background-repeat: no-repeat;
    background-position: center 4rem;
    background-size: auto 100%;
}
.bg5 {
    background-image: url(./static/img/art.dc12c73.png);
    background-size: auto 100%;
    background-repeat: no-repeat;
    background-position: center;
}
.bg6 {
    background-image: url(./static/img/dance.71bf665.jpg);
    background-size: contain;
    background-position: 0 -3rem;
}
.bg7 {
    background-image: url(./static/img/opera.74f797f.png);
    background-repeat: no-repeat;
